import * as React from 'react';
import ReactDOM from 'react-dom';
import { emptyFn, inject } from './helper';
import { GetKeyByValueType, Omit } from '../common/interface';

interface IShowProps {
    visible?: boolean;
    duration?: number;  // autoclose
}

interface IShowConfig<P extends IShowProps> {
    Component: React.ComponentClass<P>;
    transitionDuration: number;
    autoClose: boolean;
    props: P;
    forcedProps?: Partial<P>;
    injectClosePropNames?: Array<GetKeyByValueType<P, Function>>;
    deepInjectClose?: Array<[any | any[], string]>;
}

/**
 * 
 * @param Component 
 * @param transitionDuration 
 * @param props props
 * @param forcedProps props
 * @param autoClose 
 * @param injectClosePropNames closeproppropFunction
 */
export function showWithTransition<T extends IShowProps>({
    Component,
    transitionDuration,
    autoClose,
    props,
    forcedProps,
    injectClosePropNames = [],
    deepInjectClose = []
}: IShowConfig<T>) {

    const div = document.createElement('div');
    document.body.appendChild(div);

    let timer: ReturnType<typeof setTimeout>;
    let closed = false;

    injectClosePropNames.forEach(key => {
        overrideSingle(props, key as string);
    });

    deepInjectClose.forEach(([reference, key]) => {
        // 
        if (Array.isArray(reference)) {
            reference.forEach(obj => overrideSingle(obj, key));
            return;
        }

        // 
        overrideSingle(reference, key);
    });

    let currentProps: T = {
        ...props,
        ...forcedProps,
        visible: true
    };

    // close
    function overrideSingle(obj: any, key: string) {
        const originalValue = obj[key] || emptyFn;
        obj[key] = inject(originalValue as Function, close);
    }

    function update(newProps: Omit<T, 'visible'>) {
        if (closed) {
            return;
        }
        currentProps = {
            ...currentProps,
            ...newProps,
            ...forcedProps,
            visible: true
        };
        render(currentProps);
    }

    function close() {
        if (closed) {
            return;
        }
        closed = true;
        clearTimeout(timer);
        currentProps = {
            ...currentProps,
            visible: false
        };
        render(currentProps);
        // dom
        setTimeout(() => {
            ReactDOM.unmountComponentAtNode(div);
            div.remove();
        }, transitionDuration + 20);    // render
    }

    function render(props: T) {
        ReactDOM.render(<Component {...props} />, div);
    }

    render(currentProps);

    if (autoClose) {
        timer = setTimeout(close, props.duration || 2000);
    }

    return { update, close };
}
