import * as React from 'react';
import { emptyFn } from '../../utils/helper';
import { IMenuProps, IPopoutMetrics } from './interface';

type TMenuItemContext = {
    itemStyle?: React.CSSProperties;
    itemClassName?: string;
    clickHandler: (id: string) => void;
    selectedIds: string[];
};

export const MenuItemContext = React.createContext<TMenuItemContext>({
    itemStyle: undefined,
    clickHandler: emptyFn,
    selectedIds: []
});

type TSubMenuContext = {
    zIndex: number;
    visible: boolean;
    type: IMenuProps['type'];
    horizontalPlacement: IPopoutMetrics['horizontalPlacement'];    // 
};

// bodyz-indexz-index
export const SubMenuContext = React.createContext<TSubMenuContext>({
    zIndex: 10000,
    visible: true,
    type: 'normal',
    horizontalPlacement: 'right'
});

type TContextMenuContext = {
    visible: boolean;
    onClick: () => void;
};

export const ContextMenuContext = React.createContext<TContextMenuContext>({
    visible: true,
    onClick: emptyFn,
});
